import React, { useEffect, useState } from "react";
import axios from "axios";

import WorksItem from "../components/_worksItem";
import Modal from "../components/_modal";

export default function Works() {

  const [data, setData ] = useState([]);

  useEffect(()=>{
    axios.get('https://dl.dropboxusercontent.com/s/s9f44kfrlr359r7/works.json')    
    .then(res => {
      setData(res.data)
    })
  }, [])

  return (
    <div className="row my-5 bg-dark">
      <div className="col-md-3 bg-dark text-white">
        <h1 className="my-5 text-md-end me-md-5">Works</h1>
      </div>
      <div className="col-md-7 offset-md-1 py-5">
        <div class="row row-cols-1 row-cols-sm-2 row-cols-md-1 row-cols-lg-2 row-cols-xl-3 g-4">
          {data.map((item, index)=>(
            <>
            <WorksItem imagePath={item.imagePath} projectName={item.project} key={index} targetID={`work${item.ID}`}/>
            <Modal ID={`work${item.ID}`} imagePath={item.imagePath} projectName={item.project}/>
            </>
          ))}          
        </div>
      </div>
    </div>
  );
}
